*{
  padding:0; margin:0;
  box-sizing: border-box;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 300;
}
li{list-style: none;}
a{text-decoration: none;}
::selection {
  background: #333;
  color: #fcbd57;
}
::-moz-selection {
  background: #333;
  color: #fcbd57;
}
body, html , .accordion-button {cursor: url(data:image/png;base64,)5 5, url(data:image/png;base64,)5 5, auto} a {cursor: url(data:image/png;base64,)5 5, url(data:image/png;base64,)5 5, auto}

body {
  position: relative;
  overflow: hidden;
  width:100vw; height:100vh;
  background:url(/PORTFOLIO/static/media/bg-2.9a87f73b.jpg) center no-repeat;
  background-size: cover;
}
header{
  z-index:200;
  position:fixed;
  left:0;top:0;
  background:url(/PORTFOLIO/static/media/bg-2.9a87f73b.jpg) center no-repeat;
  background-size: cover;
  width:7%;
  height:100vh;
  border-right:1px solid #000;
}
header .gohome{
  width:40%;
  position: absolute;
  left:50%; top:20px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity:0;
  transition:0.5s;
}
header .gohome.show{
  opacity:1;
}
header .logo{
  width:100%;
  position:absolute;
  left:50%; bottom:10px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display:flex;
  flex-direction: column;
  align-items: center;
}
header .logo a{
  font-weight: 700;
  font-style: italic;
  text-align: center;
  width:80%;
  font-size:12px;
  background:rgba(0, 0, 0, 0.4);
  color:#fff;
  text-shadow: 2px 1px 0 #000;
  margin:5px 0;
  padding:6px 0;
  transition:0.2s;
}
header .logo a:visited{
  color:#fff;
}
header .logo a:hover{
  background:rgba(255, 255, 255, 0.4);
}
header > #menu{
  position:absolute;
  left:50%;top:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  background:#000;
  width:50px; height:50px;
  border-radius: 25px;
  overflow: hidden;
}
header > #menu::after{
  content: '';
  width:100%;height:100%;
  background:#fff;
  mix-blend-mode: difference;
  position:absolute;
  right:-100%;top:0;
  transition:0.4s;
}
header > #menu:hover::after{
  right:0; top:0;
}
header > #menu span{
  display:flex;
  width:20px;
  height:2px;
  background:#fff;
  transition: 0.2s;
  border-radius: 10px;
}
header > #menu span:nth-of-type(1){
  position: absolute;
  left:50%; top:20px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
header > #menu span:nth-of-type(2){
  position: absolute;
  left:50%; bottom:20px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
#menu.show span:nth-of-type(1){
  left:50%; top:50%;
  -webkit-transform: translate(-50%,-50%) rotate(45deg);
          transform: translate(-50%,-50%) rotate(45deg);
}
#menu.show span:nth-of-type(2){
  left:50%; top:50%;
  -webkit-transform: translate(-50%,-50%) rotate(-45deg);
          transform: translate(-50%,-50%) rotate(-45deg);
}
nav{
  z-index:19;
  position: fixed;
  left:7%; top:0;
  width:97vw;
  height:100vh;
  background: rgba(0,0,0,0.8);
  transition: 0.3s;
  opacity: 0;
  pointer-events: none;
  display:flex;
}
.nav-con{
  padding:50px 40px 40px;
  z-index: 19;
  position: absolute;
  left:0;top:0;
  width:35%;
  height:100%;
  background: #fff;
  -webkit-transform:translateX(-100%);
          transform:translateX(-100%);
  transition: 0.5s;
}
.nav-con-2{
  display:absolute;
  right:0; top:0;
  width:100%; height:100%;
}
nav .nav-con ul{
  display:flex;
  justify-content: space-around;
  align-items: center;
  padding:0 5px;
}
nav .nav-con ul li{
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
nav .nav-con ul li span{
  font-size:12px;
}
nav .nav-con ul li a{
  padding: 0 5px;
  position: relative;
  font-weight: 700;
  font-size:20px;
  color:#000;
}
nav .nav-con ul li a:visited{
  color:#000;
}
nav .nav-con ul li a::before {  
  -webkit-transform: scaleX(0);  
          transform: scaleX(0);
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
}
nav .nav-con ul li a:hover::before {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}
nav .nav-con ul li a::before {
  content: " ";
  display: block;
  position: absolute;
  top: 0; right: 0; bottom: 0; left: 0;
  inset: 0 0 0 0;
  background: #fcbd57;
  z-index: -1;
  transition: -webkit-transform .3s ease;
  transition: transform .3s ease;
  transition: transform .3s ease, -webkit-transform .3s ease;
}
@media (orientation: landscape) {
  body {
    grid-auto-flow: column;
  }
}
nav.show{
  left:7%; top:0;
  opacity: 1;
  pointer-events:visible;
}
nav.show .nav-con{
  -webkit-transform:translateX(0);
          transform:translateX(0);
}
.nav-con figure img{
  width:130px;
}
