.accordion .accordion-item {
  border-bottom: 1px solid #e5e5e5;
}
.accordion .accordion-item button.true {
  border-bottom: 1px solid #e46d44;
}
.accordion button {
  position: relative;
  display: block;
  text-align: left;
  width: 100%;
  padding: 1em 0;
  color: #999;
  font-size: 1.15rem;
  font-weight: 400;
  border: none;
  background: none;
  outline: none;
}
.accordion button:hover {
  color: #e46d44;
}
.accordion button:hover::after, .accordion button:focus::after {
  color: #e46d44;
  border: 1px solid #e46d44;
}
.accordion button .accordion-title {
  padding: 1em 1.5em 1em 0;
  font-size:16px;
  font-weight: 500;
}
.accordion button .icon {
  display: inline-block;
  position: absolute;
  top: 18px;
  right: 0;
  width: 22px;
  height: 22px;
  border: 1px solid;
  border-radius: 22px;
}
.accordion button .icon::before {
  display: block;
  position: absolute;
  content: "";
  top: 9px;
  left: 5px;
  width: 10px;
  height: 2px;
  background: currentColor;
}
.accordion button .icon::after {
  display: block;
  position: absolute;
  content: "";
  top: 5px;
  left: 9px;
  width: 2px;
  height: 10px;
  background: currentColor;
}
.accordion button.true {
  color: #e46d44;
}
.accordion button.true .icon::after {
  width: 0;
}
.accordion .accordion-content {
  font-family: 'Cafe24SsurroundAir';
  font-size:16px;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: opacity 200ms linear, max-height 200ms linear;
}
.accordion-content.true {
  opacity: 1;
  max-height: 30em;
  transition: all 200ms linear;
}
.accordion .accordion-content p {
  font-size:14px;
  font-weight: 300;
  margin: 2em 0;
  line-height: 1.8;
}
